/* =========================================GovernmentAndLGUSection */
.GovernmentAndLGU{
  /* border: 1px solid rgb(0, 183, 255); */
  width: 100%;
  height: auto;
  font-size: 50px;
  margin-bottom: 10%;
}
#GovernmentAndLGUId{
  /* border: 1px solid rgb(255, 8, 8); */
}
.line1{
  text-align: left;
  font: normal normal bold 49px/93px Myanmar Text;
  letter-spacing: 1.81px;
  color: white;
  text-shadow:
    -1px -1px 0 #800000,
    1px -1px 0 #800000,
    -1px 1px 0 #800000,
    1px 1px 0 #800000;  
  opacity: 1;
}
.line2{
  text-align: left;
  font: normal normal bold 49px/93px Myanmar Text;
  letter-spacing: 1.81px;
  color: #800000;
  text-shadow: 0px 3px 6px #00000069;
  margin-top: -50px;
  margin-left: 5px;
  opacity: 1;
}
.GovernmentAndLGU_Row1{
  /* border: 1px solid rgb(0, 16, 163); */
  padding-top: 10%;
  text-align: center;
  font: normal normal 600 40px/60px Poppins;
  letter-spacing: 0px;
  color: #800000;
  opacity: 1
}
.GovernmentAndLGU_Row2{
  /* border: 1px solid rgb(141, 0, 0); */
  text-align: center;
  font: normal normal normal 22px/44px Poppins;
  letter-spacing: 0.66px;
  color: #000000;
  opacity: 1;
}
.GovernmentAndLGU_Row3{
  /* border: 1px solid black; */
  margin-top: 1%;
  margin-bottom: 5%;
  display: flex;
  padding-left: 5%;
  
}
.GovernmentAndLGU_box{
  /* border: 1px solid black; */
  margin-top: 20px;
  margin-left: 30px;
  margin-right: 30px;
  width: auto;
  height: 400px;
  background: #FFFFFF 0% 0% no-repeat padding-box;
  box-shadow: 0px 3px 26px #00000012;
  border-radius: 20px;
  opacity: 1;
}
.GovernmentAndLGU_text{
  /* border: 1px solid red; */
  text-align: center;
  font: normal normal bold 25px/35px Poppins;
  letter-spacing: 0.75px;
  color: #800000;
  text-transform: uppercase;
  opacity: 1;
  margin: 30px;
  margin-top: 15%;
}
.GovernmentAndLGU_img{
  /* border: 1px solid black; */
  width: 80%;
  height: 50%;
  margin: 10%;
}
.GovernmentAndLGU_box:hover .text-overlay{
  display: flex;
  backdrop-filter: blur(5px);
}
.text-overlay{
  /* border: 1px solid rgb(6, 172, 0); */
  position: absolute;
  left: inherit;
  top: inherit;
  margin-left: -1.1%;
  margin-top: 1%;
  width: 20%;
  height: 35%;
  display: flex;
  flex-direction: column;
  background-color: rgba(255, 255, 255, 0.596);
  display: none;
}
.text-overlay p{
  text-align: left;
  font: normal normal normal 20px/30px Poppins;
  letter-spacing: 0px;
  opacity: 1;
}
.text-container{
  /* border: 1px solid red; */
  height: 80%;
  margin-top: 5%;
  font: normal normal normal 20px/30px Poppins;
  color: #727272;
  opacity: 1;
}
.readMoreContainer{
  margin-top: 2%;
  font: normal normal normal 20px/30px Poppins;
  color: #FF3939 !important;
  opacity: 1; 
}
