/* ================================================SystemsIntegrationSection */
.SystemsIntegration{
  /* border: 1px solid rgb(0, 255, 106); */
  height: auto;
  padding-left: 5%; 
  font-size: 50px;
}
.SystemsIntegration_container{
  /* border: 1px solid black; */
  width: 95.3%;
  padding-bottom: 5%;
  padding-top: 5%;
}
.SystemsIntegration_text{
  /* border: 1px solid red; */
  text-align: left;
  font: normal normal 600 40px/46px Poppins;
  letter-spacing: 1.2px;
  color: #800000;
  text-transform: capitalize;
  opacity: 1;
  width: 50%;
}
.SystemsIntegration_btn_container{
  /* border: 1px solid rgb(0, 4, 255); */
  text-align: left;
  font: normal normal normal 22px/33px Poppins;
  letter-spacing: 0px;
  color: #000000;
  opacity: 1;
  /* padding-right: -50px; */
  padding-bottom: 5%;
  width: 100%;
  display: flex;
  
}
.SystemsIntegration_btn{
  background: #FFFFFF 0% 0% no-repeat padding-box;
  border: 1px solid #707070;
  border-radius: 58px !important;
  opacity: 1;
  width: 33% !important;
  height: 100px !important;
  margin-top: 5% !important;
  margin-right: 1% !important;
}
.SystemsIntegration_btn:hover{
  color:#f7f7f7;
  border: 1px solid #bd0000;
  background-color: #bd0000;
  transition: all ease 0.2s;
}
.SystemsIntegration_long_btn{
  width: 65% ;
  height: 105px;
  background: #FFFFFF 0% 0% no-repeat padding-box;
  border: 1px solid #707070;
  border-radius: 58px !important;
  opacity: 1;
  margin-top: 5%;
}
.SystemsIntegration_long_btn:hover{
  border: 3px solid #bd0000;
  transition: all ease 0.1s;
}
.SystemsIntegration_long_btn_p{
  text-align: center;
  font: normal normal normal 22px/28px Poppins;
  letter-spacing: 0.66px;
  color: #000000;
  opacity: 0.53;
  margin-bottom: 5px;
  position: relative;
}
